import React from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { closePricingModal } from "../../../utils/close_modal";
import { CLOUDPATH_TAGGSHOP } from "../../../actions/api";

export default function GettingStarted() {
  const dispatch = useDispatch();

  return (
    <>
      <div className="controller" id="globalPage">
        <div className="action" id="globalPageswitch">
          <div className="switch_page p-5 d-flex align-items-center justify-content-center">
            <div className="switch_page_inner card card-custom p-lg-10 ">
              <div className="row align-items-center">
                <div className="col-lg-6">
                  <div className="switch_image">
                    <video autoPlay="autoplay" id="audioTag" muted="" loop="" style={{ pointerEvents: "none" }} > <source src="https://cloud.taggbox.com/taggshop/app/assets/media/products/display.mp4" type="video/mp4" /> </video>
                  </div>
                </div>
                <div className="col-lg-6 mt-15 mt-lg-0 switch_text">
                  <div className="switch_text_inner">
                    <h2 className="font-weight-boldest"> <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/products-icon/display.svg`} /> Widget </h2>
                    <p className="mb-5 text-dark"> Build and Widget live social media walls in events(virtual or live), signages &amp; in store screens. </p>
                    <ul className="content">
                      <li>Aggregate Live Feeds From Social Media</li>
                      <li>Design Social Wall With Studio</li>
                      <li>Easily Display Social Media Wall Anywhere</li>
                    </ul>{" "}
                    <ul className="switch_tags">
                      <li> <span>#LiveEvents</span> </li> <li> <span>#VirtualEvents</span> </li> <li> <span>#HybridEvents</span> </li> <li> <span>#In-StoreDisplay</span> </li> <li> <span>#DoohAds</span> </li>
                    </ul>{" "}
                    <Link to="/walls/home" onClick={() => closePricingModal(dispatch)} className="btn btn-primary btn-lg btn-min-w mr-4 mb-4" > Let's Get Started </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>{" "}
        </div>
      </div>
    </>
  );
}
