import { toggleAddFeedsSection, toggleAppsMoreSection, toggleCustomizationSection, toggleManageSection, togglePlanSection, toggleThemesSection, togglePublishSection } from "../actions/show_modal_Action";


export const closePricingModal = (dispatch) => {
   dispatch(togglePlanSection(false))
   dispatch(toggleAppsMoreSection(false))
   dispatch(toggleAddFeedsSection(false))
   dispatch(toggleManageSection(false))
   dispatch(toggleThemesSection(false))
   dispatch(toggleCustomizationSection(false))
   dispatch(togglePublishSection(false))


}